.logo {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 2rem;
  font-weight: bold;
}
#logo-img {
  margin-top: 20px;
  margin-left: -50px;
}
.header {
  height: 10vh;
  margin-bottom: 20px;
}
.logo-text {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 2rem;
  margin-left: 10px;
}
#creator {
  margin-top: -25px;
  margin-bottom: 5%;
  margin-left: 60px;
  font-size: 0.8rem;
  font-family: "Courier New", Courier, monospace;
}
.danger {
  font-size: 0.7rem;
  font-weight: 700;
  width: 15vw;
  height: 7vh;
  margin: 5px;
}
@media screen and (max-width: 600px) {
  .header {
    height: 7vh;
  }
  #creator {
    margin-top: -20px;
    margin-left: 10px;
    font-size: 0.5rem;
  }
  #logo-img {
    display: none;
  }
  .logo-text{
    padding: 5px;
    font-size: 1.2rem;
  }
  .danger {
    font-size: 0.7rem;
    font-weight: 700;
    width: 15vw;
    height: 4vh;
    margin: 5px;
  }
}